@use "../base/variables" as v;
@use "../base/mixins" as m;

.header {
	background-color: v.$verde;
	@include m.tablet {
		&.fijo {
			left: 0;
			top: 0;
			position: fixed;
			width: 100%;
			box-shadow: 0.1rem 0.2rem 0.3rem black;
			border-radius: 5rem;
		}
	}

	h1 {
		color: v.$blanco;
		text-align: center;
		@include m.tablet {
			margin: 0;
		}
	}
	.contenido-header {
		padding: 2rem;
		@include m.tablet {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
	}
}
.navegacion_principal {
	@include m.tablet {
		display: flex;
		gap: 2rem;
	}
	a {
		text-align: center;
		display: block;
		color: v.$blanco;
		font-size: 2.4rem;
		margin-bottom: 1rem;
		@include m.tablet {
			margin: 0;
		}

		&:hover {
			color: v.$amarillo;
		}
	}
}
