@use "../base/variables" as v;
@use "../base/mixins" as m;

.lineUp {
	background-color: v.$rosa;
	padding: 5rem 0;
	h3,
	p {
		text-align: center;
		color: v.$blanco;
	}
	.dia {
		text-transform: uppercase;
		margin: 5rem 0;
	}
}
.escenarios-contenedor {
	@include m.tablet {
		@include m.grid(2, 5rem);
	}
	p {
		color: v.$negro;
	}
}
.escenario {
	padding-top: 2rem;
	.nombre-escenario {
		&::after {
			content: "";
			display: block;
			width: 20rem;
			height: 10rem;
			background-size: 15rem;
			background-repeat: no-repeat;
			background-position: center center;
			margin: 2rem auto 0 auto;
		}
	}
}

.edm .nombre-escenario:after {
	background-image: url(../../../build/img/icono_edm.avif);
}
.rock .nombre-escenario:after {
	background-image: url(../../../build/img/icono_rock.avif);
}
.calendario {
	@include m.resetearLista;
	li {
		padding: 2.5rem;
		font-weight: 700;
		span {
			font-weight: 400;
		}
	}
}
.bg-amarillo {
	background-color: v.$amarillo;
	li:nth-child(odd) {
		background-color: darken(v.$amarillo, 25%);
	}
}
.bg-verde {
	background-color: v.$verde;
	li:nth-child(odd) {
		background-color: darken(v.$verde, 10%);
	}
}
